.Projects {
    /* background-color: #22282f; */
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #13bbff;
    flex-direction: column;
}

.project-card {
    background-color: #1E1E1E;
    background-color: #2a353b;
    color: #fff;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
    padding: 20px;
    margin: 20px;
    max-width:420px;
    /* width: 100%; */
    border-radius: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    overflow: hidden;
    transform: scale(1);
    /* transition: transform 0.3s ease-in-out, box-shadow 0.3s ease-in-out; */
    transition: transform 0.3s ease-in-out, box-shadow 0.3s ease-in-out;
}
.project-card:hover {
    transform: scale(1.05); /* Scale up to 105% on hover */
    box-shadow: 0 15px 30px rgba(10, 10, 10, 0.9); /* Increase the shadow on hover */
}

 
.project-card img {
    max-width: 350px;
    height: auto;
    /* margin-bottom: 20px; */
    border-radius: 10px;
    animation: float 4s ease-in-out infinite;
}

@keyframes float {

    0%,
    100% {
        transform: translateY(0);
    }

    50% {
        transform: translateY(-8px);
    }
}

.project-info {
    flex: 1;
    display: flex;
    flex-direction: column;
     margin: 20px 0;
    /* background-color: red; */
}

.project-info h2 {
    font-size: 24px;
    margin-bottom: 10px;
}

.project-languages {
    height: 50px;
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    gap: 5px;
    margin: 20px 0;
}

.language-label {
    background-color: rgba(0, 0, 0, 0.3);
    color: #fff;
    height: 25px;
    padding: 5px 10px;
    border-radius: 5px;
    font-size: 14px;
    margin-right: 5px;
}

.buttons {
    display: flex;
    gap: 10px;
}

.buttons a {
    text-decoration: none;
    color: #fff;
    background-color: #007acc;
    padding: 10px 20px;
    border-radius: 5px;
    font-weight: bold;
    transition: background-color 0.3s ease;
}

.buttons a:hover {
    background-color: #005c9f;
}



.ProjectsHeader {
    margin: 1em 0;
    border-bottom: 1px solid #13bbff;
    width: max-content;
}

.projectContent {
    padding: 20px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    max-width: 1900px;
    margin: auto;
}

 @media screen and (max-width:768px) {

    .project-card{
        margin:0;
        padding: 0;
    }

    .projectContent{
        gap: 2em;
        padding: 0;
        margin: 0;
    }
    
 }